/* eslint-disable require-jsdoc */
// Imports
import * as PIXI from "pixi.js";
import Cursor from "./components/cursor.js";

// Cursor
const cursor = new Cursor();
cursor.init();

// PIXI
let width = window.innerWidth;
let height = window.innerHeight;

const app = new PIXI.Application(width, height, {
    autoResize: true,
    resolution: devicePixelRatio,
    transparent: true
});
document.body.append(app.view);

app.stage.interactive = true;

const container = new PIXI.Container();
app.stage.addChild(container);


let displacementSprite;
let bg;
let bgMask;
let mouse;
let light;

PIXI.Loader.shared.add('dpmap', require("../assets/dpmap/displace.png"))
    .add(
        'bg', require("../assets/banana.jpg")
    )
    .add(
        'mask', require("../assets/mask.jpg")
    )
    .load((loader, resources) => {
        displacementSprite = new PIXI.Sprite(
            resources.dpmap.texture
        );
        const displacementFilter = new PIXI.filters.DisplacementFilter(
            displacementSprite
        );
        container.addChild(displacementSprite);

        displacementFilter.scale.x = 6;
        displacementFilter.scale.y = 6


        //Image
        bg = new PIXI.Sprite(
            resources.bg.texture
        );
        bg.width = width;
        bg.height = height;
        bg.alpha = 1;

        container.addChild(bg);


        // light Effect
        light = new PIXI.Graphics()
        light.beginFill(0x000000, .15)
        light.drawRect(0, 0, width, height)

        container.addChild(light)


        // Mouse Filter
        mouse = new PIXI.Sprite(
            resources.mask.texture
        );
        mouse.x = 100
        mouse.y = 100
        mouse.scale.x = 7
        mouse.scale.y = 7

        container.addChild(mouse)

        bgMask = new PIXI.Sprite(
            resources.bg.texture
        );
        bgMask.width = width;
        bgMask.height = height;

        bgMask.alpha = .8;
        bgMask.filters = [displacementFilter];
        bgMask.mask = mouse

        container.addChild(bgMask);



    });

window.addEventListener('mousemove', e => {
    // bg.rotation.set(e.clientX / 100, e.clientY / 100)
    displacementSprite.position.set(e.clientX - displacementSprite.width / 2, e.clientY - displacementSprite.height / 2);
    mouse.position.set(e.clientX - mouse.width / 2, e.clientY - mouse.height / 2);
})


// Resize
window.addEventListener("resize", resize);

function resize() {
    width = window.innerWidth;
    height = window.innerHeight;
    app.renderer.resize(width, height);
}

resize();